import base from './base';

export const loginIn$ = (params: object) => base.post('jauth', params, { useToken: false });

export const fetchCanUseFarms$ = (params: object) => base.get('farms', params, { useToken: true });

export const uploadImgs$ = (params: object) => base.post('imagesupload', params, { useFarmCode: true, useToken: true });

// 
export const fetchTodoTaskRuns$ = (params: object) => base.get('todotaskruns', params, { useFarmCode: true, useToken: true });

// ()
export const fetchTodoTaskRunsDetail$ = (params: object) => base.get('todotaskruns', params, { useFarmCode: true, useToken: true, useId: true });

// 
export const fetchUsersList$ = (params: object) => base.get('users', params, { useFarmCode: true, useToken: true });

// -
export const fetchCowlitesList$ = (params: object) => base.get('cowlites', params, { useFarmCode: true, useToken: true });

// -
export const fetchPensList$ = (params: object) => base.get('pens', params, { useFarmCode: true, useToken: true });

// 
export const fetchCalfsList$ = (params: object) => base.get('cows', params, { useFarmCode: true, useToken: true });

// 
export const fetchCalfsDetail$ = (params: any) => {
    const options = {
        useFarmCode: true,
        useToken: true,
        useId: true
    }

    return base.get('cows', params, options);
};

